.navbar {
  z-index: 4000;
  background: #3c3735 !important;
  border-bottom: none !important;
  font-size: 22px;
  height: 70px;
  color: #fff;
}
.navbar .phones {
  float: left;
  height: 65px;
  padding-top: 10px;
}
.navbar .phone {
  display: block;
  color: #c0c0c0;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 1.2px;
}
.navbar.navbar-default .navbar-brand {
  color: #fff;
  font-size: 36px;
  font-family: 'Lato';
  margin-right: 100px;
  padding: 20px 0 0 0;
  border-bottom: 1px solid #fff;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.navbar.navbar-default .navbar-brand i {
  color: #fff;
  font-size: 29px;
  padding-bottom: 10px;
  display: inline-block;
}
.navbar.navbar-default .navbar-brand:hover {
  color: #ffa500;
  border-color: #6db33f !important;
}
.navbar.navbar-default .navbar-brand:hover i {
  color: #6db33f;
}
.navbar.navbar-default .navbar-brand:focus {
  color: #fff;
}
.navbar.navbar-default .navbar-brand:focus i {
  color: #fff;
}
.navbar .navbar-nav > li > a {
  padding: 23px 10px;
  font-family: 'Open Sans';
  color: #fff !important;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 14px;
  -webkit-transition: background 0.1s ease-in-out;
  transition: background 0.1s ease-in-out;
}
.navbar .navbar-nav > li > a:hover {
  color: #fff;
  background: #6db33f !important;
}
.main {
  min-height: calc(100vh - 70px);
  min-width: 100vw;
  background-size: cover;
  background: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.main .main-content {
  z-index: 2;
  text-align: center;
}
.main .main-description {
  color: #fff;
  font-size: 24px;
  font-family: 'Open Sans';
}
.main .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: calc(100vh - 70px);
  min-width: 100vw;
  background-color: rgba(0,0,0,0.6);
  z-index: 1;
}
.main h1 {
  color: #fff;
  font-family: 'Open Sans';
  text-transform: uppercase;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
}
.main h1 strong {
  color: #6db33f;
}
.main .main-button {
  background: #6db33f;
  color: #fff;
  width: 300px;
  height: 60px;
  border: none;
  font-size: 20px;
  font-weight: bold;
  -webkit-transition: background 0.1s ease-in-out;
  transition: background 0.1s ease-in-out;
  margin-top: 50px;
}
.main .main-button:hover {
  background: #578f32;
}
.discount {
  height: 90px;
  background: #6db33f;
  text-align: center;
  font-size: 26px;
  color: #fff;
  padding-top: 25px;
}
.about {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.about .description {
  -ms-flex-preferred-size: 60%;
      flex-basis: 60%;
  padding-right: 100px;
}
.about .description p {
  font-size: 22px;
}
.about div {
  font-size: 20px;
}
.about img {
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
  border: 2px solid #008000;
}
body {
  height: 1000px;
  margin-top: 70px;
}
h1 {
  font-family: 'Lato';
  color: #fff;
  text-align: center;
  font-size: 72px;
}
h2 {
  text-transform: uppercase;
}
.about {
  padding: 100px 0;
}
.bx-wrapper {
  margin: 0;
  position: absolute;
  top: 70px;
  left: 0;
  width: 100vw;
  border: none;
}
.bx-wrapper .bxslider img {
  width: 100vw;
  height: calc(100vh - 70px);
}
